@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("..fonts/lato/Lato-Regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0 auto;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  object-fit: contain;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  font-family: "Oswald", "Lato", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header {
    font-family: "Oswald", "Lato", "Arial", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .header {
    font-family: "Oswald", "Lato", "Arial", sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
}

.header__container {
  margin: 0 auto;
  max-width: 320px;
  min-height: 65px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header__container {
    padding-bottom: 62px;
    max-width: 768px;
    min-height: 52px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1440px) {
  .header__container {
    max-width: 1220px;
    padding: 54px 110px 74px;
    min-height: 60px;
    justify-content: space-between;
  }
}

.header__navigation-logo {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  padding: 10px 20px;
}
@media (min-width: 768px) {
  .header__navigation-logo {
    width: 174px;
    padding-left: 30px;
  }
}
@media (min-width: 1440px) {
  .header__navigation-logo {
    display: flex;
    width: 202px;
    margin: 0;
    padding: 0;
  }
}

.header__navigation-icon:nth-child(2) {
  padding-left: 57px;
}
@media (min-width: 768px) {
  .header__navigation-icon:nth-child(2) {
    padding-left: 12px;
  }
}

.header__navigation-menu {
  position: relative;
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__navigation-menu {
    padding-right: 30px;
    width: 440px;
  }
}
@media (min-width: 1440px) {
  .header__navigation-menu {
    width: 478px;
    min-height: 40px;
  }
}

.header__navigation-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: stretch;
  text-align: center;
}

.header__navigation-menu--active .header__navigation-toggle {
  position: absolute;
  width: 24px;
  height: 20px;
  border: 0;
  top: -35px;
  right: 20px;
  display: block;
  background: url("../img/svg/nav_cross.svg");
  background-repeat: no-repeat;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__navigation-menu--active .header__navigation-toggle {
    display: none;
  }
}

.header__navigation-menu--inactive .header__navigation-toggle {
  position: absolute;
  width: 24px;
  height: 20px;
  border: 0;
  top: -35px;
  right: 20px;
  display: block;
  background: url("../img/svg/nav_hamburger.svg");
  background-repeat: no-repeat;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__navigation-menu--inactive .header__navigation-toggle {
    display: none;
  }
}

.header__navigation-menu--active .header__navigation-list {
  position: absolute;
  display: flex;
  background-color: #ffffff;
  width: 320px;
  animation: nav_animation 0.6s;
}
@keyframes nav_animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.header__navigation-menu--inactive .header__navigation-list {
  display: none;
}
@media (min-width: 768px) {
  .header__navigation-menu--inactive .header__navigation-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(3, max-content);
    justify-items: center;
    justify-content: space-around;
    column-gap: 10px;
  }
}

.header__navigation-menu--nojs .header__navigation-toggle {
  display: none;
}

.header__navigation-link {
  display: block;
  color: inherit;
  padding: 22px 20px 24px;
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .header__navigation-link {
    position: relative;
    padding: 18px 0 12px;
    border: none;
  }
  .header__navigation-link--checked::before {
    position: absolute;
    height: 2px;
    bottom: 1px;
    left: 0;
    right: 0;
    background-color: #68b738;
    content: "";
  }
}

